<template>
  <div id="wrap" class="renting">
    <div class="header">
      <span class="type">以租代售</span>
      <span class="car">{{car_notice_number}}</span>
    </div>
    <div class="input-content">
      <div>
        <span>需求车辆数（台）</span>
        <input type="tel" v-model="num">
      </div>
      <div>
        <span>租赁年限（年）</span>
        <input type="tel" v-model="day">
      </div>
       <div>
        <span>首付比例（%）</span>
        <input type="tel" v-model="downPayment">
      </div>
       <div>
        <span>尾款（元）</span>
        <input type="tel" v-model="finalPayment">
      </div>
    </div>
    <div class="offer-content">
      <div class="offer-wrap">
        <span class="project bolder">报价项目</span>
        <span class="price bolder">单价</span>
        <span class="sum bolder">合计</span>
      </div>
      <div class="offer-wrap">
        <span class="project">首付金额（元）</span>
        <span class="price">{{down_money}}</span>
        <span class="sum">{{down_money_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">尾款（元）</span>
        <span class="price">{{finalPayment}}</span>
        <span class="sum">{{lease_term_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">车辆技改（元/辆）</span>
        <span class="price">{{vehicle}}</span>
        <span class="sum">{{Vehicle_sum}}</span>
      </div>
      <div class="offer-wrap">
        <span class="project">车辆租金（元/月/辆）</span>
        <span class="price">{{car_price}}</span>
        <span class="sum">{{car_price_sum}}</span>
      </div>
    </div>
    <div class="footer-btn">
      <mt-button class="nextBtn" type="primary" @click="count">计算</mt-button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      car_notice_number: '',
      vehicle: '',
      Vehicle_sum: '',
      lease_term_sum: '',
      car_price: 0,
      car_price_sum: '',
      down_money: 0,
      down_money_sum: '',
      num: 0,
      day: 0,
      downPayment: 0,
      finalPayment: 0,
      selectData: {}
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      document.title = '在线报价'
    })
  },
  computed: {
    getCarInfo() {
      return this.$store.getters['GET_CAR_INFO']
    },
    getServiceInfo() {
      return this.$store.getters['GET_SERVICE_INFO']
    },
    getVehicle() {
      return this.$store.getters['GET_VEHICLE_INFO']
    }
  },
  mounted() {
    this.car_notice_number = this.getCarInfo.car_notice_number
    this.vehicle = this.getVehicle.price === undefined ? 0 : this.getVehicle.price
    this.selectData.main_id = this.getCarInfo.id
    this.selectData.price_mode = this.getServiceInfo.choosePriceMode
    this.selectData.service_price = this.getServiceInfo.price
  },
  methods: {
    count() {
      let num = this.num
      let that = this
      this.Vehicle_sum = this.vehicle * num
      this.down_money_sum = this.down_money * num
      this.selectData.down_payment_ratio = this.downPayment / 100
      this.selectData.lease_term = this.day
      this.selectData.final_payment = this.finalPayment
      this.$axios({
        url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-price',
        method: 'post',
        data: this.selectData,
        transformRequest: [function (data) {
          var ret = '';
          for (var it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        if (res.data.errcode === 0) {
          that.car_price = res.data.data.car_price
          that.car_price_sum = that.car_price * num
          that.down_money = res.data.data.down_payment
          that.down_money_sum = that.down_money * num
          that.lease_term_sum = that.finalPayment * num
        } else {
          that.$toast(res.data.msg)
        }
      })
    }
  }
}
</script>



